.form {
  @extend %section-indent;
  
    padding: {
    left: 1rem;
    right: 1rem;
  }

  &__heading {
    color: $primary;
    text-align: center;
  }

  &__body {
    margin: 0 auto;
    max-width: 22rem;
    text-align: center;
  }

  &__item {
    margin-top: 1.5rem;
  }

  &__button {
    margin-top: 1rem;
  }

  &_horizontal {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: 576px) {
      flex-direction: row;
      align-items: flex-start;
    }

    .form__item {
      flex-grow: 1;

      margin: {
        top: 0;
        bottom: 1rem;
      }

      @media screen and (min-width: 576px) {
        margin: {
          bottom: 0;
          right: 1rem;
        }
      }
    }

    .form__button {
      margin-top: 0.5rem;

      @media screen and (min-width: 576px) {
        max-width: 33%;
      }
    }
  }

  &_wide {
    .form__body {
      max-width: 40rem;
    }
  }
}
